.footer {
  background-color: $basic-black;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
  margin-top: auto;
  padding: 64px 0 32px;
  &__logo {
    height: 40px;
    filter: brightness(100);
  }
  &__infos {
    @include typographies(subtitle);
    color: $basic-white;
  }
}

@include tablet-and-mobile {
  .footer {
    gap: 24px;
    padding: 64px 0 40px;

    &__logo {
      height: 32px;
      filter: brightness(100);
    }
  }
}
